@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap");

body {
	padding: 0;
	margin: 0;
	font-family: "Inter", sans-serif;
}

header {
	text-align: center;
	margin: 0 auto;
	font-size: 14px;
	font-weight: 500;
	width: 94%;
	max-width: 375px;
	padding-top: 4%;
	padding-bottom: 8%;
}

#appTitle {
	color: #3730a3;
	padding-bottom: 4%;
}

#appTitle #appTitleContainer {
	margin: 0 auto;
	width: 185px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

#appIntro {
	color: #818cf8;
	padding-left: 1%;
	padding-right: 1%;
}

.textDiv {
	width: 94%;
	max-width: 375px;
	margin: 0 auto;
}

#textDivTitle {
	font-size: 14px;
	color: #374151;
	margin-bottom: 2%;
}

#resetSpan {
	float: right;
	font-size: 12px;
	color: #1d4ed8;
	padding: 0.6%;
	padding-right: 2%;
}

#textAreaDiv {
	margin: 0 auto;
	border: 1px #d1d5db solid;
	border-radius: 4px;
	padding: 1%;
}

#txtArea {
	width: 100%;
	height: 30%;
	font-size: 14px;
	font-weight: 400;
	font-family: "Inter", sans-serif;
	color: #6b7280;
	resize: none;
	outline: none;
	border: none;
}

.resultsDiv {
	width: 94%;
	max-width: 375px;
	margin: 0 auto;
	margin-top: 6%;
}

#resultsDivTitle {
	font-size: 14px;
	color: #374151;
	padding-bottom: 5px;
}

#resultsDivTitle span {
	float: right;
	color: #9ca3af;
}

#rangesTable {
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
	margin-top: 2%;
}

#rangesTable th {
	font-size: 16px;
	font-weight: 500;
	color: #5b21b6;
	text-align: left;
	padding: 4%;
	background-color: #ede9fe;
}

#rangesTable th:first-child {
	border-radius: 4px 0 0 4px;
}

#rangesTable th:last-child {
	border-radius: 0 4px 4px 0;
}

#rangesTable td {
	font-size: 14px;
	font-weight: 400;
	color: #6b7280;
	padding: 4%;
	border-bottom: 1px solid #dfdfdf;
}

@media only screen and (min-width: 426px) and (max-width: 767px) {
	.textDiv {
		width: 92%;
	}
}

